<template>
  <div
    id="boosts"
    :style="{ 'background-color': $store.state.currentPlanet.color }"
    v-if="!superRain"
  >
    <h2>Boosts</h2>
    <div class="icon close" @click="$store.commit('BoxClicked', 0)">
      <i class="fas fa-times"></i>
    </div>
    <ul>
      <li
        v-for="boost in $store.state.boosts"
        :key="boost.name"
        @click="Boosts(boost.name, boost.price)"
      >
        <div class="leftSide">
          <div class="icon">
            <i :class="boost.icon"></i>
          </div>
        </div>
        <div class="rightSide">
          <h3>{{ boost.name }}</h3>
          <p class="smallText">{{ boost.description }}</p>
          <div class="price">
            {{ Math.round(boost.price / 0.5) * 0.5 }}
            <span class="bolt">
              <i class="fas fa-bolt"></i>
            </span>
          </div>
          <div class="quantity smallText">{{ boost.amount }}x</div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "Boosts",
  methods: {
    Boosts(type, price) {
      this.$store.commit("Boosts", {
        type: type,
        price: price,
      });
    },
  },
  computed: {
    superRain() {
      return this.$store.state.superRain;
    },
  },
};
</script>

