<template>
  <div
    id="cards"
    :style="{ 'background-color': $store.state.currentPlanet.color }"
  >
    <h2>Your cards</h2>
    <div class="icon close" @click="$store.commit('OpenCards')">
      <i class="fas fa-times"></i>
    </div>
    <div class="listOfCards">
      <div class="card">
        <div class="top">
          <img :src="cardImage" />
        </div>
        <div class="bottom">
          <h3>{{ cardName }}</h3>
          <p>{{ cardText }}</p>
        </div>
      </div>
      <div class="previousCard icon buttons" @click="PreviousCard()">
        <font-awesome-icon icon="fa-solid fa-caret-left" />
      </div>
      <div class="nextCard icon buttons" @click="NextCard()">
        <font-awesome-icon icon="fa-solid fa-caret-right" />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Cards",
  data() {
    return {
      cardName: "",
      cardText: "",
      cardImage: "",
      id: 0,
    };
  },
  created() {
    this.CardInfo();
  },
  methods: {
    NextCard() {
      if (this.id < this.$store.state.boughtPlanets.length) {
        this.id++;
      }
      if (this.id == this.$store.state.boughtPlanets.length) {
        this.id = 0;
      }
      this.CardInfo();
    },
    PreviousCard() {
      if (this.id == 0) {
        this.id = this.$store.state.boughtPlanets.length;
      }
      this.id--;
      this.CardInfo();
    },
    CardInfo() {
      this.cardName = this.$store.state.boughtPlanets[this.id].name;
      this.cardText = this.$store.state.boughtPlanets[this.id].text;
      this.cardImage = this.$store.state.boughtPlanets[this.id].img;
    },
  },
};
</script>

<style lang="scss" scoped>
#cards {
  z-index: 50;
  position: absolute;
  width: 80%;
  height: 350px;
  @include centerAbsoluteXandY();
  @include boxBorder();

  border-radius: 5px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  flex-direction: column;
  .icon {
    font-size: 1.5rem;
  }
  .close {
    font-size: 2rem;
    position: absolute;
    right: 15px;
    top: 15px;
    &:hover {
      cursor: pointer;
    }
  }
  h2 {
    height: max-content;
    margin-top: 25px;
    text-align: center;
    width: 100%;
    font-weight: bold;
  }
  .listOfCards {
    width: 80%;
    margin-top: 30px;
    height: 225px;

    .card {
      margin-top: 30px;
      display: grid;
      grid-template-columns: 1fr;
      grid-template-rows: 100px auto;
      background-color: $white;
      color: $black;
      border-radius: 5px;
      @include boxShadow();
      margin-top: 0;
      overflow: hidden;
      .top {
        background-color: $black;
        @include flexCenter();
        img {
          width: 80px;
        }
      }
      .bottom {
        padding: 1rem;
        display: flex;
        flex-wrap: wrap;
        align-content: flex-start;
        height: 100%;
        max-height: 100px;
        overflow-y: scroll;
        h3 {
          width: 100%;
          font-weight: bold;
        }
        p {
          width: 100%;
        }
      }
    }
  }
  .buttons {
    width: 50px;
    height: 50px;
    background-color: $black;
    position: absolute;
    @include flexCenter();
    font-size: 1.5rem;
    @include centerAbsoluteY();
    border-radius: 5px;
    border: 2px solid $white;
    &:hover {
      cursor: pointer;
    }
  }
  .previousCard {
    left: -20px;
  }
  .nextCard {
    right: -20px;
  }
}
</style>
