<template>
  <section id="top">
    <!--BatteryIcon-->
    <div class="battery">
      <div class="icon" v-if="battery == 0">
        <font-awesome-icon icon="fa-solid fa-battery-empty" />
      </div>
      <div class="icon" v-else-if="battery == 0.25">
        <font-awesome-icon icon="fa-solid fa-battery-quarter" />
      </div>
      <div class="icon" v-else-if="battery == 0.5">
        <font-awesome-icon icon="fa-solid fa-battery-half" />
      </div>
      <div class="icon" v-else-if="battery == 0.75">
        <font-awesome-icon icon="fa-solid fa-battery-three-quarters" />
      </div>
      <div class="icon" v-else-if="battery == 1">
        <font-awesome-icon icon="fa-solid fa-battery-full" />
      </div>
    </div>

    <!--Heading-->
    <div class="heading">
      <h1>{{ $store.state.currentPlanet.name }}</h1>
      <p>
        {{ currentEnergy }}
        <span class="bolt">
          <i class="fas fa-bolt"></i>
        </span>
      </p>
      <div class="smallText">{{ $store.state.pointsForSec }}/s</div>
    </div>

    <!--OpenNavigation-->
    <div
      class="openNav"
      @click="$store.commit('OpenNav')"
      :style="{ 'background-color': $store.state.currentPlanet.color }"
    >
      <div class="icon">
        <i class="fas fa-bars"></i>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: "TopSection",
  computed: {
    battery() {
      return this.$store.state.battery;
    },
    currentEnergy() {
      this.$store.commit("BatteryChecker");
      return Math.round(this.$store.state.currentEnergy / 0.5) * 0.5;
    },
  },

};
</script>

