<template>
  <div id="music">
    <div class="icon" @click="AudioOn()" v-if="!this.$store.state.audioOn">
      <font-awesome-icon icon="fa-solid fa-volume-xmark" />
    </div>
    <div class="icon" @click="AudioOff()" v-if="this.$store.state.audioOn">
      <font-awesome-icon icon="fa-solid fa-volume-high" />
    </div>
  </div>
</template>

<script>
export default {
  name: "Music",
  data() {
    return {
      sound: new Audio(require("@/assets/sounds/mainMusic.mp3")),
    };
  },
  methods: {
    AudioOn() {
      this.$store.state.audioOn = true;

      this.sound.volume = 0.5;
      this.sound.play();
      this.sound.loop = true;
    },
    AudioOff() {
      this.$store.state.audioOn = false;
      this.sound.pause();
      this.sound.volume = 0;
    },
  }
};
</script>

<style lang="scss" scoped>
#music {
  position: absolute;
  bottom: 120px;
  .icon {
    font-size: 2rem;
    left: 15px;
    padding: 25px;
    &:hover {
      cursor: pointer;
    }
  }
}
</style>
