<template>
  <div
    id="planet"
    @click="$store.commit('Click'), BoltPosition(), BoltOpacity(), Audio()"
  >
    <div class="planetImage">
      <img :src="$store.state.currentPlanet.img" alt="planet" />
      <div
        class="energyPoint"
        :style="{
          left: randomLeft + 'px',
          top: randomTop + 'px',
          opacity: boltOpacity,
        }"
      >
        <p>
          +
          {{ $store.state.pointsForClick }}
          <span class="bolt">
            <i class="fas fa-bolt"></i>
          </span>
        </p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Planet",
  data() {
    return {
      randomLeft: 0,
      randomTop: 0,
      boltOpacity: 0,
    };
  },
  methods: {
    BoltPosition() {
      this.randomLeft = Math.floor(Math.random() * 150);
      this.randomTop = Math.floor(Math.random() * 150);
    },
    BoltOpacity() {
      this.boltOpacity = 1;
      setTimeout(() => {
        this.boltOpacity = 0;
      }, 1000);
    },
    Audio() {
      if (this.$store.state.audioOn == true) {
        let audio = new Audio(require("@/assets/sounds/energyPoint.wav"));
        audio.volume = 0.2;
        audio.play();
      }
    },
  }
};
</script>

<style lang="scss" scoped>
#planet {
  @include flexCenter();
  &:hover {
    cursor: pointer;
  }
  .planetImage {
    width: 200px;
    position: relative;
    img {
      width: 100%;
      height: auto;
      &::selection {
        background-color: transparent;
      }
    }
  }
  .energyPoint {
    position: absolute;
    left: 0;
    top: 0;
    color: $white;
    font-weight: bold;
    p {
      font-size: 2rem;
      &::selection {
        background-color: transparent;
      }
    }
    .bolt {
      color: $yellow;
    }
  }
}
</style>
