<template>
  <div
    id="achievements"
    :style="{ 'background-color': $store.state.currentPlanet.color }"
  >
    <h2>Achievements</h2>
    <div class="icon close" @click="$store.commit('BoxClicked', 0)">
      <i class="fas fa-times"></i>
    </div>
    <ul>
      <li v-for="achievement in AvailableAchievements" :key="achievement.name">
        <div class="leftSide">
          <div class="icon trophy">
            <i class="fas fa-trophy"></i>
          </div>
        </div>
        <div class="rightSide">
          <h3>{{ achievement.name }}</h3>
        </div>
      </li>
      <div class="noTrophies" v-if="noTrophies">
        <h2>You don't have any trophies!</h2>
      </div>
    </ul>
  </div>
</template>

<script>
export default {
  name: "Achievements",
  data() {
    return {
      noTrophies: true,
    };
  },
  computed: {
    AvailableAchievements: function () {
      let returningArray = this.$store.state.achievements.filter(
        (e) => e.available === true
      );
      if (returningArray.length == 0) {
        this.noTrophies = true;
      } else {
        this.noTrophies = false;
        return returningArray;
      }
    },
  },
};
</script>

